.button {
    display: inline-block;
    width: 100%;
    padding: 22px 48px;
    background-color: #fff;
    color: #1E1E1E;
    font-family: "RF Dewi";
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 1.25;
    transition:  0.2s ease-out;
    /* white-space: nowrap; */
    /* width: auto; */
    border: 1px solid transparent;
}

.button:hover {
    background-color: #FC2F6C;
    border: 1px solid #FC2F6C;
    color: #fff;
}


.button--bordered {
    border: 1px solid #1E1E1E;
}

@media(max-width: 840px) {
    .button {
        font-size: 12px;
        line-height: 1.2;
        padding: 18px 40px;
    }
}

.button--theme-dark {
    background-color: #1E1E1E;
    color: #fff;
}

.button--theme-pink {
    background-color: #FC2F6C;
    color: #fff;
}